import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './LottoItem.scss';

class LottoItem extends Component {
  delayPageLotto = pageName => {
    const {
      history: { push }
    } = this.props;
    const { common } = this.props.gold.root;
    common.loadingDelay(push, pageName);
  };

  render() {
    const { delayPageLotto } = this;
    return (
      <li className="lotto">
        <div className="box">
          11
          <div className="round left">2222</div>
        </div>

        <Link
          to={{
            pathname: ''
          }}
          onClick={() => delayPageLotto('gold')}
        >
          <div className="summary">
            <div className="line">
              <p className="t">로또 대박집</p>
              <p className="u">ReactApp, 동행복권</p>
              <p className="s">2019</p>
            </div>
          </div>
        </Link>
      </li>
    );
  }
}

export default LottoItem;
